import { seo } from "projectSettings"
import React from "react"
import { GatsbySeo } from "gatsby-plugin-next-seo"
import Layout from "globalLayoutsIndexPage"
import styled from "styled-components"
import { MDXRenderer } from "gatsby-plugin-mdx"
import { GatsbyImage, getImage } from "gatsby-plugin-image"
import HomePageCover from "projectOrganisms/HomePageCover"
import { graphql } from "gatsby"
import openGraphImageUrl from "projectSrc/assets/open-graph/default.jpg"

const PageStyle = styled.div`
  margin-top: -48px;
  @media (max-width: 576px) {
    margin-top: -64px;
  }
  .cover {
    border-radius: 4px;
    background-color: ${({ theme }) => theme.background_secondary};
    box-shadow: 1px 0px 0px ${({ theme }) => theme.separator_non_opaque},
      -1px 0px 0px ${({ theme }) => theme.separator_non_opaque},
      0px 1px 0px ${({ theme }) => theme.separator_non_opaque},
      0px -1px 0px ${({ theme }) => theme.separator_non_opaque};
  }
  .title-wrapper {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    flex-flow: wrap;
    .experience-info {
      color: ${({ theme }) => theme.text_tertiary};
    }
  }
`

export default function PortfolioSingle(props) {
  const PageData = props.data.mdx
  const PageDataExtra = props.data.mdx.frontmatter.extra

  function getCover() {
    return PageData.frontmatter.screenshot ? (
      <GatsbyImage
        alt={`${PageData.frontmatter.title}`}
        image={getImage(PageData.frontmatter.screenshot)}
        className="cover"
      />
    ) : (
      <GatsbyImage
        alt={`${PageData.frontmatter.title}`}
        image={getImage(PageData.frontmatter.cover)}
        className="cover"
      />
    )
  }

  return (
    <>
      <GatsbySeo
        title={`${seo.siteShortSeoPrefix} ${PageData.frontmatter.title}`}
        description={PageData.excerpt}
        canonical={seo.siteUrl}
        openGraph={{
          url: props.location.href,
          title: PageData.frontmatter.title,
          description: PageData.excerpt,
          images: [
            {
              url: `${seo.siteUrl}${openGraphImageUrl}`,
              width: 1200,
              height: 630,
              alt: seo.siteSeoPrefix,
            },
          ],
        }}
      />
      <Layout>
        <section>
          <HomePageCover>
            <h1 style={{ display: "flex", flexDirection: "column" }}>
              <span className="xxxLarge-high-title mb-16 fw-bold">
                {PageData.frontmatter.title}{" "}
              </span>
              <span className="xxxLarge-h1" style={{ maxWidth: "768px" }}>
                {PageData.frontmatter.caption}
              </span>
            </h1>
          </HomePageCover>
        </section>
        <PageStyle
          as="article"
          className="container-fluid"
          style={{ maxWidth: "992px" }}
        >
          {getCover()}
          <div className="title-wrapper mt-48">
            <h2 className="large-h1 mb-16">Detailed information</h2>
            {PageDataExtra.workExternalLink && (
              <span className="experience-info mb-16">
                Watch in:{" "}
                <a href={PageDataExtra.workExternalLink.href} target="blank">
                  {PageDataExtra.workExternalLink.name}
                </a>
              </span>
            )}
          </div>
          <hr className="mt-0"></hr>
          <MDXRenderer>{PageData.body}</MDXRenderer>
        </PageStyle>
      </Layout>
    </>
  )
}

export const query = graphql`
  query($slug: String!) {
    mdx(slug: { eq: $slug }) {
      ...PortfolioSinglePage
    }
  }
`
